import Bmob from "hydrogen-js-sdk";
import loading from "@/components/Loading/index.vue"
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		loading,
		faIcon,
		uniSegmentedControl,
		uniNavBar,
		uniIcon
	},
	data() {
		return {
			loading: true,
			items: [
				'销售客户',
				'供货商',
			],
			current: 0,
			people: null, //获得人员数组
			is_producer: false,
			is_custom:false
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');
		
		if (options.type == "producer") {
			that.is_producer = true,
			that.current = 1,
			that.load_data("producers")
		}else if(options.type == "custom"){
			that.is_custom = true,
			that.current = 0,
			that.load_data("customs")
		}
	},

	onShow() {
		uni.removeStorageSync("customs")
		uni.removeStorageSync("custom_type")
		if (that.current == 0) {
			that.load_data("customs")
		} else {
			that.load_data("producers")
		}

	},
	onUnload() {
		search_text =""
	},
	methods: {
		
		//选择此供货商
		select_this(type,producer) {
			uni.setStorageSync(type, producer)
			uni.navigateBack({
				delta: 1
			})
		},
		
		//前去添加员工
		goto_add(){
			uni.showActionSheet({
				itemList: ['新增客户', '新增供货商'],
				success: function(res) {
					console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					if (res.tapIndex == 0) {
						uni.navigateTo({
							url: "add/add?type=customs"
						})
					} else {
						uni.navigateTo({
							url: "add/add?type=producers"
						})
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		
		//输入内容筛选
		input_confirm(e){
			search_text = e.detail.value
			if (this.current == 0) {
				that.load_data("customs")
			} else {
				that.load_data("producers")
			}
		},

		//tab点击
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index

				if (this.current == 0) {
					that.load_data("customs")
				} else {
					that.load_data("producers")
				}
			}
		},

		//加载数据
		load_data(type) {
			const query = Bmob.Query(type);
			query.equalTo("parent", "==", uid);
			query.limit(500);
			if (search_text) {
				if (type == "customs") {
					query.equalTo("custom_name", "==", {
						"$regex": "" + search_text + ".*"
					});
				} else {
					query.equalTo("producer_name", "==", {
						"$regex": "" + search_text + ".*"
					});
				}

			}
			query.find().then(res => {
				console.log(res)
				that.loading = false;
				that.people = res;
			});
		},

	}
}
